const axios = require('axios')

const basicAuth = (telegramId) => {
  const username = 'user' + telegramId
  const token = btoa(username + ':' + btoa(username))
  return 'Basic ' + token
}

const API_URL = 'https://9db7-195-64-239-77.ngrok-free.app/api/'

const Api = (telegramId) => {
  return {
    getUser: () => {
      return axios({
        method: 'get',
        url: API_URL + 'profile/',
        headers: {
          Authorization: basicAuth(telegramId)
        }
      })
    }
  }
}

export default Api
