import './Tap.sass'
import {useState} from "react";

const Tap = ({setBalance}) => {

  const [fill, setFill] = useState(.0)

  return (
    <div className="Tap">
      <div className="big-shadow" style={{
        background:
          "linear-gradient(180deg, rgba(255,255,0,0.7) 0%, rgba(255,255,0,0.7) " + Math.max(0, 120*fill - 20) +
          "%, rgba(136,136,136,0.5) " + Math.min(100, 120*fill) + "%, rgba(136,136,136,0.5) 100%)"
      }} />
      <img src="/images/button_little_shadow.svg" style={{width: '53vw'}} alt="Little shadow" />
      <img src="/images/button.png"
           className="image"
           alt="Button"
           onClick={() => setBalance(prevState => prevState + 1)} />
    </div>
  )
}

export default Tap;
