import './BottomMenu.sass'
import {useState} from "react";
import BottomMenuPage from "../BottomMenuPage/BottomMenuPage";
import TasksPage from "../TasksPage/TasksPage";
import ShopPage from "../ShopPage/ShopPage";
import FriendsPage from "../FriendsPage/FriendsPage";

const BottomMenu = () => {

  const buttonNames = ['Home', 'Tasks', 'Shop', 'Friends']
  const pages = {
    Tasks: <TasksPage />,
    Shop: <ShopPage />,
    Friends: <FriendsPage />,
  }

  const [activeButton, setActiveButton] = useState('Home')

  return (
    <div className="bottom-menu-container">
      {pages.hasOwnProperty(activeButton) ? <BottomMenuPage content={pages[activeButton]} /> : null}
      <div className="bottom-menu">
        {
          buttonNames.map(btnName => (
            <button key={btnName} onClick={() => {setActiveButton(btnName)}}>
              <img src={"/images/icons/"
                + (btnName === activeButton ? "active" : "inactive")
                + "/" + btnName + ".png"} alt="btn" />
              <p className={btnName === activeButton ? "text-active" : ""}>{btnName}</p>
            </button>
          ))
        }
      </div>
    </div>
  )
}

export default BottomMenu
